a {
  color: inherit;
  margin: 0 0 5px 0 !important;
}
#green{
  background: #A3FFC2;
}
#red{
  background: #FFA3A3;

}
.toVisible{
  &:hover{
    background-color: #8bb8df !important;
  }
}
#zhurnal_button{
  background: #A3D8FF;
}

.row{
  #no{
    cursor: not-allowed ;
  }
  .panel{
    margin: 15px 15px;
    border-color: transparent !important;
    .panel-body{
      height: 200%;
      overflow-x: auto;
      display: flex;
      .col-sm-6 {
        padding-right: 5%;
        .thumbnail {
          width: 120%;
          .caption{
            h2{
              text-align: unset;
            }
          }
          background-color: #f5f5f8;
          box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.2);
          border-radius: 15px;
          p {
            #red{
              background: #FFA3A3;
              border: 1px solid #E09C9C;
            }
            #green{
              background: #A3FFC2;
              border: 1px solid rgba(81, 216, 78, 0.5);
            }
            #count {
              float: right;
            }
          }
        }
      }
    }
  }
}
hr{
  border-top: 1px solid rgba(0,0,0,0.2);
}
.caption{
  h5,h2{
    font-size: 18px !important;
    font-weight: bold !important;
    text-align: center;
    color: rgba(0,0,0,0.5);
  }
}
.modal-body{
  a{
      background:linear-gradient(180deg, #4393D8 0%, rgba(50, 147, 230, 0.458333) 99.99%, rgba(35, 146, 241, 0) 100%);
  }
}
.thumbnail {
  background-color: #f5f5f8;
  box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  p {
    #count {
      float: right;
    }
  }
}